@import '../colors';

* {
    margin: 0;
    padding: 0;
}

.outerFloat {
    display: flex;
    flex-direction: column;
    padding: 0
}

.floating {

    background-color: $main;
    color: white;
    position: fixed;
    margin-top: 5rem;
    z-index: 100;
    border-radius: 1rem 0rem 0rem 1rem;
    height: 8rem;
    width: 2rem;
    align-self: flex-end;

}

.innerFloat {
    font-family: 'Kraftan', sans-serif;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    padding-right: 0rem;
    padding-top: 3.5rem;
    padding-left: 1.5rem;
    height: 0rem;
    width: 7rem;
    margin-top: 2rem;

}

.postBody{
    display:flex;
    flex-direction:row;
    height:75vh;
    
    h4{
        margin-top: 2rem;
        z-index:0;
        color:white;    
        font-weight: bold;
        font-size: 4rem;
        align-self:flex-end;
        margin-bottom: 1rem;
        margin-left: 1rem;
        font-family: 'Kraftan', sans-serif;
    }
    img{
        filter: brightness(50%);
        width:100%;
        height:75vh;
        object-fit:cover;
    }
}


.backgroundImg{
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 100%;
}

*{
    // border:2px solid red;
}

.blogBody{
    display:flex;
    flex-direction:row;
    padding:2rem;
}

tr:nth-child(even) {
    background-color: #e1dcdcdf;
}
td{
    padding:1rem;
    
}

tr:hover {
    background-color: $main;
}

.desc{
    text-align: justify;
    margin-right: 1rem;
    margin-bottom:2rem;
}

.tableOfContents{
    margin:1rem;
}   
.topicHeading{
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.blogImages{
    // border:2px solid $main;
    display:flex;
    flex-direction:column;
    width: 100vw;
    justify-content: space-evenly;
    // flex-wrap:wrap;
    // justify-content:space-around;
    // margin:1rem;
    img{
        width:100%;
        height:fit-content;
        margin-bottom: 1.5rem;

    }
}

.blogContent{
    width:225%;
}